export default {
    
    data() {      
        return {
            url:"",
           username: '',
           PWD: '',
        }
    },
    methods: {
        goSecurity: function() {
            const url='./changePWD';
            uni.navigateTo({ url });
        },
        goChildSetting: function() {
            const url='./childrenInfo';
            uni.navigateTo({ url });
        },
        goSwitch: function() {
            const url='./WiFiInfoLogin';
			
            uni.navigateTo({ url });
        },
    },
    mounted() {
        this.username = this.$root.$mp.query.username
        this.PWD = this.$root.$mp.query.PWD
    }
}
